
.titleMessage{
    color:#007C66;
    margin:5% 5% ;
    display:flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}
.box {
    padding: 1rem;
    margin: 1rem;
    box-shadow: 0 0 1rem #00000025 inset;
}

.grid-responsive {
    display:grid;
    grid-template-columns: repeat(autofit, minmax(200px, 1fr));
    gap: 1rem;
}